@import '/src/shared/styles';

.home {
  @extend %pageContainer;
}

.listItemWrap {
  width: 100%;
  margin-bottom: 10px;

  &.overflow {
    //width: calc(100% + 11px);
  }

  &:last-of-type {
    margin-bottom: 0;
  }
}

.listContainer {
  width: calc(100% - 40px);
  height: calc(100% - 175px);
  padding: 0 0 20px;
  overflow-y: auto;
  overflow-x: hidden;
  position: absolute;
  bottom: 0;
  left: 20px;
  box-sizing: border-box;
  opacity: 0;
  transition: 1s;
}

.focus {
  z-index: 1;
  opacity: 1;
}

.addBtn {
  bottom: 20px;
  left: 20px;
  position: fixed;
  z-index: 1;

  &.rotate {
    transform: rotate(90deg);
  }

  @include media('>phone') {
    bottom: calc(50% - 354px);
    left: calc(50% - 189px);
  }
}

.inputContainer {
  background: white;
  width: 100%;
  height: 100%;
  position: fixed;
  padding: 20px 0;
  box-sizing: border-box;
  bottom: 0;
  z-index: 1;
  transform: translateY(100%);
}
